import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Icon from "@mui/material/Icon";

// Material Kit 2 React components
import MKBox from "components/MKBox";
import MKAvatar from "components/MKAvatar";
import MKButton from "components/MKButton";
import MKTypography from "components/MKTypography";
import { useState } from "react";
// Images
import profilePicture from "assets/images/me.jpeg";

import React from "react";

function Profile() {
  const PROJECTS = 4;
  const SCIENCE = 2;
  const [quote, setQuote] = useState(); // Initial state for quote
  const [author, setAuthor] = useState(); // Initial state for quote
  const fetchQuote = async () => {
    try {
      const response = await fetch('https://api.quotable.io/random');

      if (!response.ok) {
        // Handle HTTP error status
        throw new Error('Failed to fetch quote');
      }

      const data = await response.json();

      if (!data || !data.content || !data.author) {
        // Handle unexpected response format
        throw new Error('Invalid quote data received');
      }
      setQuote(data.content); // Update quote state with fetched content
      setAuthor(data.author);
    } catch (error) {
      setQuote('Failed to fetch quote. Please try again later.');
      setAuthor('Eric Nazarenus');
    }
  };

  return (
    <MKBox component="section" py={{ xs: 6, sm: 12 }}>
      <Container>
        <Grid container item xs={12} justifyContent="center" mx="auto">
          <MKBox mt={{ xs: -16, md: -20 }} textAlign="center">
            <MKAvatar src={profilePicture} alt="Eric Nazarenus" size="xxl" shadow="xl" />
          </MKBox>
          <Grid container justifyContent="center" py={6}>
            <Grid item xs={12} md={7} mx={{ xs: "auto", sm: 6, md: 1 }}>
              <MKBox display="flex" justifyContent="space-between" alignItems="center" mb={1}>
                <MKTypography variant="h3">Eric Nazarenus</MKTypography>
                <MKButton variant="outlined" color="info" size="small" onClick={fetchQuote}>
                  New Quote
                </MKButton>
              </MKBox>
              <Grid container spacing={3} mb={3}>
                <Grid item>
                  <MKTypography component="span" variant="body2" fontWeight="bold">
                    {SCIENCE}&nbsp;
                  </MKTypography>
                  <MKTypography component="span" variant="body2" color="text">
                    Scientific Publications
                  </MKTypography>
                </Grid>
                <Grid item>
                  <MKTypography component="span" variant="body2" fontWeight="bold">
                    {PROJECTS}&nbsp;
                  </MKTypography>
                  <MKTypography component="span" variant="body2" color="text">
                    Personal Projects
                  </MKTypography>
                </Grid>
              </Grid>
              <MKTypography variant="body1" fontWeight="light" color="text">
                {quote !== undefined ? quote : "The world is but a canvas to the imagination."} <br />
                {author !== undefined ? "- " + author : "- Henry David Thoreau"}
                <MKTypography
                  component="a"
                  href="/Portfolio/#/cv"
                  variant="body1"
                  fontWeight="light"
                  color="info"
                  mt={3}
                  sx={{
                    width: "max-content",
                    display: "flex",
                    alignItems: "center",

                    "& .material-icons-round": {
                      transform: `translateX(3px)`,
                      transition: "transform 0.2s cubic-bezier(0.34, 1.61, 0.7, 1.3)",
                    },

                    "&:hover .material-icons-round, &:focus .material-icons-round": {
                      transform: `translateX(6px)`,
                    },
                  }}
                >
                  More About Me <Icon sx={{ fontWeight: "bold" }}>arrow_forward</Icon>
                </MKTypography>
              </MKTypography>
            </Grid>
          </Grid>
        </Grid>
      </Container>
    </MKBox>
  );
}

export default Profile;
