import React, { useRef, useState, useEffect } from 'react';
import axios from 'axios';
import MKButton from 'components/MKButton';
const DrawingApp = ({ onPredictionUpdate }) => {
    const canvasRef = useRef(null);
    const [isDrawing, setIsDrawing] = useState(false);

    useEffect(() => {
        clearCanvas();
    }, []);

    const startDrawing = (event) => {
        setIsDrawing(true);
        draw(event);
    };

    const stopDrawing = () => {
        setIsDrawing(false);
    };

    const draw = (event) => {
        if (!isDrawing) return;
        const canvas = canvasRef.current;
        const context = canvas.getContext('2d');
        const rect = canvas.getBoundingClientRect();

        context.beginPath();
        context.arc(event.clientX - rect.left, event.clientY - rect.top, 12, 0, 2 * Math.PI);
        context.fillStyle = '#000';
        context.strokeStyle = "rgba(1, 1, 1, 0)";
        context.shadowColor = "gray";
        context.shadowBlur = 2;
        context.fill();
        context.stroke();
    };

    const clearCanvas = () => {
        const canvas = canvasRef.current;
        const context = canvas.getContext('2d');
        context.fillStyle = 'white';
        context.fillRect(0, 0, canvas.width, canvas.height);
    };

    const clearDrawing = () => {
        clearCanvas();
        onPredictionUpdate(-1)
    };

    const sendDrawing = async () => {
        const canvas = canvasRef.current;
        canvas.toBlob(async (blob) => {
            if (blob) {
                const formData = new FormData();
                formData.append('image', blob, 'drawing.png');

                try {
                    const response = await axios.post('http://68.183.221.255/predict', formData);
                    console.log('Prediction:', response.data);
                    console.log(response.data.cnn_predictions[0]);
                    onPredictionUpdate(response.data);
                } catch (error) {
                    console.error('Error sending image:', error);
                }
            } else {
                console.error('Error creating blob from canvas');
            }
        }, 'image/png');
    };

    return (
        <div >
            <canvas
                ref={canvasRef}
                width={280}
                height={280}
                style={{ border: '1px solid #000' }}
                onMouseDown={(e) => startDrawing(e)}
                onMouseMove={(e) => draw(e)}
                onMouseUp={() => stopDrawing()}
            />
            <div>
                <MKButton sx={{ mr: 3, }} onClick={clearDrawing} variant="outlined" color="error">Clear Canvas</MKButton>
                <MKButton onClick={sendDrawing} variant="gradient" color="success">Classify</MKButton>
            </div>
        </div>
    );
};

export default DrawingApp;
