import React, { useState, useEffect } from 'react';
import DrawingApp from 'components/Classify/DrawingApp';
import MKTypography from 'components/MKTypography';
import MKBox from 'components/MKBox';
import { Accordion, AccordionSummary, AccordionDetails, Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
// @mui material components
import Container from "@mui/material/Container";
import AppBar from "@mui/material/AppBar";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import MKButton from 'components/MKButton';
import { Link } from 'react-router-dom';

function Classify() {
    const [activeTab, setActiveTab] = useState(0);
    const handleTabType = (event, newValue) => { setActiveTab(newValue); };

    const [prediction, setPrediction] = useState(
        {
            cnn_predictions: [Array(10).fill("0")],
            mlp_predictions: [Array(10).fill("0")],
        });
    const [isPredicted, setIsPredicted] = useState(false);
    const handlePredictionUpdate = (newPrediction) => {
        if (newPrediction !== -1) {
            setPrediction(newPrediction);
            setIsPredicted(true);
            console.log(indexOfMax(prediction));
        }
        else {
            setIsPredicted(false)
        }
    };

    const [maxPredictionIndex, setMaxPredictionIndex] = useState(null);

    useEffect(() => {
        // Find the index of the maximum prediction value in the array
        if (prediction.cnn_predictions[0] && prediction.cnn_predictions[0].length > 0 && activeTab === 0) {
            const maxIndex = prediction.cnn_predictions[0].reduce((maxIndex, currentValue, currentIndex) => {
                if (currentIndex === 0 || currentValue > prediction.cnn_predictions[0][maxIndex]) {
                    return currentIndex;
                }
                return maxIndex;
            }, 0);
            setMaxPredictionIndex(maxIndex);
        }
        else if (prediction.mlp_predictions[0] && prediction.mlp_predictions[0].length > 0 && activeTab === 1) {
            const maxIndex = prediction.mlp_predictions[0].reduce((maxIndex, currentValue, currentIndex) => {
                if (currentIndex === 0 || currentValue > prediction.mlp_predictions[0][maxIndex]) {
                    return currentIndex;
                }
                return maxIndex;
            }, 0);
            setMaxPredictionIndex(maxIndex);
        }
    }, [prediction, activeTab]);

    return (
        <MKBox component="section" py={2}>
            <MKTypography variant="h1">Classify Numbers 0-9</MKTypography>
            <Grid container spacing={2}>
                <Grid item xs={!isPredicted ? 12 : 6} mt={3} >
                    <DrawingApp onPredictionUpdate={handlePredictionUpdate} />
                </Grid>
                {isPredicted ?
                    <Grid item xs={!isPredicted ? 12 : 6} mt={3} >
                        <TableContainer component={Paper} sx={{ maxWidth: '75%', maxHeight: "100%", margin: 'auto' }}>
                            <Container>
                                <Grid container item justifyContent="center" xs={12} lg={4} mt={1} mx="auto">
                                    <AppBar position="static">
                                        <Tabs value={activeTab} onChange={handleTabType}>
                                            <Tab label="CNN" />
                                            <Tab label="MLP" />
                                        </Tabs>
                                    </AppBar>
                                </Grid>
                            </Container>

                            <Table size="small">
                                <TableHead sx={{ display: "table-header-group" }}>
                                    <TableRow>
                                        <TableCell align="center"><strong>Number</strong></TableCell>
                                        <TableCell align="center"><strong>Prediction</strong></TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {activeTab === 0 ? prediction.cnn_predictions[0].map((item, index) => (
                                        <TableRow key={index} sx={index === maxPredictionIndex ? { backgroundColor: '#ffffcc' } : null}>
                                            <TableCell align="center">{index}</TableCell>
                                            <TableCell align="center">{item.toFixed(5)}</TableCell>
                                        </TableRow>)) :
                                        prediction.mlp_predictions[0].map((item, index) => (
                                            <TableRow key={index} sx={index === maxPredictionIndex ? { backgroundColor: '#ffffcc' } : null}>
                                                <TableCell align="center">{index}</TableCell>
                                                <TableCell align="center">{item.toFixed(5)}</TableCell>
                                            </TableRow>))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                    : <div></div>
                }

            </Grid>
            <Accordion sx={{ mt: 5 }}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                    <MKTypography variant="h6">Learn More</MKTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <MKBox width="100%">
                        <MKTypography> I trained both a Multilayer Perceptron (MLP) and a Convolutional Neural Network (CNN) on the MNIST dataset, which consists of 70000 handwritten numbers from 0 to 9. To classify these numbers, I first preprocessed the dataset by normalizing the pixel values and splitting it into training and testing sets.</MKTypography>
                        {/* Nested MLP Accordion */}
                        <Accordion sx={{ mb: 2 }}>
                            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                <MKTypography variant="subtitle1">MLP</MKTypography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <MKTypography>
                                    For the MLP model, I used Pytorch and designed a neural network with 3 fully connected layers. The input layer had 784 nodes corresponding to the flattened 28x28 pixel images. I added hidden layers with ReLU activation functions and a final output layer using the softmax to output probability scores for each number. The model was trained using cross entropy loss and the ADAM optimizer for 50 epochs.
                                </MKTypography>
                            </AccordionDetails>
                        </Accordion>

                        {/* Nested CNN Accordion */}
                        <Accordion sx={{ mb: 2 }}>
                            <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                                <MKTypography variant="subtitle1">CNN</MKTypography>
                            </AccordionSummary>
                            <AccordionDetails>
                                <MKTypography>
                                    For the CNN model, I used Pytorch to construct a network comprising 2 convolutional layers followed by a maxpooling layer to extract features hierarchically from the input images. I then connected the extracted features to 2 fully connected layers to finally compute probabilities using softmax activation. The model was trained using cross entropy loss and the ADAM optimizer for 10 epochs.
                                </MKTypography>
                            </AccordionDetails>
                        </Accordion>
                        <MKTypography sx={{ mt: 3 }}  >
                            Typically, convolutional neural networks (CNNs) tend to outperform multilayer perceptrons (MLPs) in tasks such as image classification, like the MNIST dataset, because of their efficiency in capturing local spatial patterns. However, for the straightforward task of MNIST classification, both MLPs and CNNs can yield comparable results. Notably, my basic MLP achieved a test accuracy of 96.4%, whereas my CNN achieved 98.5%.<br></br>

                            Nevertheless, I believe the CNN is exhibiting superior generalization to handwritten digits from this browser application, indicating that the model has learned more relevant features.<br></br>

                            To illustrate the performance comparison of these models and their ability to generalize to new handwritten digits, I developed a web application using React and Material-UI. This project aims to showcase and highlight the effectiveness and distinctions between MLPs and CNNs in digit recognition tasks using the MNIST dataset.                        </MKTypography>
                        <MKButton sx={{ mr: 3, mt: 1 }} component={Link} to="https://github.com/ernaz100" variant="contained" color="info">Check out the Repo</MKButton>
                    </MKBox>
                </AccordionDetails>
            </Accordion>
        </MKBox >
    );
}

function indexOfMax(arr) {
    if (arr.length === 0) {
        return -1;
    }

    let max = arr[0];
    let maxIndex = 0;

    for (let i = 1; i < arr.length; i++) {
        if (arr[i] > max) {
            maxIndex = i;
            max = arr[i];
        }
    }

    return maxIndex;
}

export default Classify;
